import React, {useState} from "react";

import "./ShopCard.css";
import {useSelector} from "react-redux";
import {getPrice} from "../../helpers/helper";

function ShopCard(props) {

    const currency = useSelector(state => state.currency);

    return (
        <div className="ShopCard col-3">
            <div className="card">
                <div className="card-body">
                    <h5 className="card-title">{ props.title }</h5>
                    <p className="card-text">{ getPrice(currency, props.price) }</p>
                    <button className="btn btn-primary" onClick={props.onButtonClick}>Move to basket</button>
                </div>
            </div>
        </div>
    )
}
export default ShopCard;
