import React from "react";

import "./BasketList.css"

import {removeLotFromBasket} from "../../store/lots/actions";
import {useDispatch, useSelector} from "react-redux";
import {getPrice} from "../../helpers/helper";

function BasketList () {

    const lots = useSelector(state => state.lots);
    const currency = useSelector(state => state.currency);
    const dispatch = useDispatch();

    return (
        <ul className="BasketList list-group list-group-numbered">
            {
                lots.data
                    .filter(lot => lot.inBasket > 0)
                    .map(lot => (
                        <li key={lot.id} className="BasketList__item list-group-item d-flex justify-content-between align-items-start">
                            <div className="ms-2 me-auto">
                                <div className="fw-bold">{lot.title}</div>
                                {getPrice(currency, lot.price)}
                            </div>
                            <button type="button" className="btn btn-danger" onClick={() => dispatch(removeLotFromBasket(lot.id))}>Remove</button>
                            <span className="BasketList__count badge bg-primary rounded-pill">{lot.inBasket}</span>
                        </li>
                    ))
            }
        </ul>
    );
}
export default BasketList;
