class LotsActions {
    static GET_LOTS_REQUESTED = "GET_LOTS_REQUESTED";
    static GET_LOTS_SUCCESS = "GET_LOTS_SUCCESS";
    static ADD_LOT_TO_BASKET = "ADD_LOT_TO_BASKET";
    static REMOVE_LOT_FROM_BASKET = "REMOVE_LOT_FROM_BASKET";
}
export default LotsActions;

export function getLots() {
    return dispatch => {
        dispatch(getLotsRequested());

        let time = setTimeout(function () {
            dispatch(getLotsSuccess([
                {
                    id: 1,
                    title: 'lot-1',
                    price: 20,
                    inBasket: 0,
                },
                {
                    id: 2,
                    title: 'lot-2',
                    price: 45,
                    inBasket: 0,
                },
                {
                    id: 3,
                    title: 'lot-3',
                    price: 67,
                    inBasket: 0,
                },
                {
                    id: 4,
                    title: 'lot-4',
                    price: 1305,
                    inBasket: 0,
                },
                {
                    id: 5,
                    title: 'lot-5',
                    price: 732,
                    inBasket: 0,
                }
            ]));
            clearTimeout(time);
        }, 1000);
    }
}

function getLotsRequested() {
    return {
        type: LotsActions.GET_LOTS_REQUESTED
    }
}

function getLotsSuccess(lots) {
    return {
        type: LotsActions.GET_LOTS_SUCCESS,
        payload: {lots}
    }
}

export function addLotToBasket(id) {
    return {
        type: LotsActions.ADD_LOT_TO_BASKET,
        payload: {id}
    }
}

export function removeLotFromBasket(id) {
    return {
        type: LotsActions.REMOVE_LOT_FROM_BASKET,
        payload: {id}
    }
}

