import axios from "axios";
import {RATES_APP_ID} from "../../index";

class CurrencyActions {
    static SET_CURRENCY = "SET_CURRENCY";
    static REFRESH_RATES_REQUESTED = "REFRESH_RATES_REQUESTED";
    static REFRESH_RATES_SUCCESS = "REFRESH_RATES_SUCCESS"
}
export default CurrencyActions;

export function setCurrency(value) {
    return {
        type: CurrencyActions.SET_CURRENCY,
        payload: {value}
    }
}

export function refreshRates() {
    return dispatch => {
        dispatch(refreshRatesRequested());

        axios.get(`https://openexchangerates.org/api/latest.json?app_id=${RATES_APP_ID}`)
            .then(res => {
                dispatch(refreshRatesSuccess(res.data.rates));
            })
            .catch(err => {
                console.log(err);
            })
    }
}

function refreshRatesRequested() {
    return {
        type: CurrencyActions.REFRESH_RATES_REQUESTED
    }
}

function refreshRatesSuccess(rates) {
    return {
        type: CurrencyActions.REFRESH_RATES_SUCCESS,
        payload: {rates}
    }
}
