import React, {useState} from "react";

import "./MainNav.css";
import {Link} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {setCurrency} from "../../store/currency/actions";

function MainNav() {

    const lots = useSelector(state => state.lots.data);
    const currency = useSelector(state => state.currency);
    const dispatch = useDispatch();
    const [showCurrencyDropdown, setShowCurrencyDropdown] = useState(false);

    const currencies = Object.keys(currency.currencies);

    let lotsCount = 0;
    lots.forEach(lot => lotsCount += lot.inBasket);

    function currencySelectHandler(value) {
        dispatch(setCurrency(value));
    }

    return (
        <div className="MainNav">
            <nav className="MainNav__nav container">
                <ul className="MainNav__list row">
                    <li className="MainNav__item col-1 d-flex justify-content-center">
                        <Link to="/">Shop</Link>
                    </li>
                    <li className="MainNav__item col-1 d-flex justify-content-center">
                        <Link to="/basket">Basket</Link>
                        { lotsCount ? <span className="badge bg-primary rounded-pill MainNav__count">{lotsCount}</span> : null }
                    </li>
                    <li className="MainNav__item MainNav__item_currency">
                        <div className="MainNav__currency" onClick={() => setShowCurrencyDropdown(!showCurrencyDropdown)}>
                            <span>{currency.value}</span>
                            {
                                showCurrencyDropdown ?
                                    <ul>
                                        {currencies.map(cur => {
                                            return <li key={cur}
                                                       onClick={() => currencySelectHandler(cur)}
                                            >{cur}</li>
                                        })}
                                    </ul> :
                                    null
                            }
                        </div>
                    </li>
                </ul>
            </nav>
        </div>
    )
}
export default MainNav;
