import React from "react";
import {useSelector} from "react-redux";

import "./Basket.css"

import BasketList from "../../components/BasketList/BasketList";
import {getPrice} from "../../helpers/helper";

function Basket() {

    const lots = useSelector(state => state.lots);
    const currency = useSelector(state => state.currency);

    let sum = 0;

    lots.data.forEach(lot => {
        const result = lot.inBasket * lot.price;
        if (result) {
            sum += result.toFixed(2);
        }
    });

    return (
        <div className="Basket">
            <div className="container">
                <h2 className="Basket__title">Basket</h2>
                <h5 className="Basket__subtitle">Sum: {getPrice(currency, sum)}</h5>
                <BasketList/>
            </div>
        </div>
    );
}
export default Basket;
