import React, {useState} from "react";

import './Shop.css';

import ShopCard from "../../components/ShopCard/ShopCard";
import {useDispatch, useSelector} from "react-redux";
import {addLotToBasket} from "../../store/lots/actions";

function Shop() {


    const lots = useSelector(state => state.lots);
    const dispatch = useDispatch();

    function shopCardButtonClickHandler(id) {
        dispatch(addLotToBasket(id));
    }

    return (
        <div className="Shop">
            <div className="container">
                <h2 className="Shop__title">Lots list</h2>
                <div className="row">
                    {lots.data.map(lot => (
                        <ShopCard key={lot.id}
                                  title={lot.title}
                                  price={lot.price}
                                  inBasket={lot.inBasket}
                                  onButtonClick={() => shopCardButtonClickHandler(lot.id)}
                        />
                    ))}
                </div>
            </div>
        </div>
    )
}
export default Shop;
