import LotsActions from "./actions";

const initialState = {
    data: [],
    loading: false,
};

export function reducer(state = initialState, action) {
    switch (action.type) {
        case LotsActions.GET_LOTS_REQUESTED:
            return {
                ...state,
                loading: true
            };
        case LotsActions.GET_LOTS_SUCCESS:
            return {
                ...state,
                data: action.payload.lots,
                loading: false,
            };
        case LotsActions.ADD_LOT_TO_BASKET:
            return {
                ...state,
                data: state.data.map(lot => {
                    return {
                        ...lot,
                        inBasket: lot.id === action.payload.id ? lot.inBasket + 1 : lot.inBasket
                    };
                })
            };
        case LotsActions.REMOVE_LOT_FROM_BASKET:
            return {
                ...state,
                data: state.data.map(lot => {
                    return {
                        ...lot,
                        inBasket: lot.id === action.payload.id ? lot.inBasket - 1 : lot.inBasket
                    };
                })
            };
        default:
            return state;
    }
}
