import CurrencyActions from "./actions";

const initialState = {
    value: "USD",
    currencies: {
        "USD": '\u0024',
        "RUB": '\u20BD',
        "EUR": '\u20AC',
        "GBP": '\u00A3',
        "JPY": '\u5186'
    },
    rates: {
        "USD": 0,
        "RUB": 0,
        "EUR": 0,
        "GBP": 0,
        "JPY": 0
    },
    loading: false,
};

export function reducer(state = initialState, action) {
    switch (action.type) {
        case CurrencyActions.SET_CURRENCY:
            return {
                ...state,
                value: action.payload.value
            };
        case CurrencyActions.REFRESH_RATES_REQUESTED:
            return {
                ...state,
                loading: true
            };
        case CurrencyActions.REFRESH_RATES_SUCCESS:
            return {
                ...state,
                rates: {...action.payload.rates},
                loading: false
            };
        default:
            return state;
    }
}
