export function getPrice(currencyState, price) {
    const totalPrice = (currencyState.rates[currencyState.value] * price).toFixed(2);
    const sign = currencyState.currencies[currencyState.value];
    let result = `${totalPrice} ${sign}`;

    if (currencyState.value !== "RUB") {
        result = `${sign} ${totalPrice}`
    }

    return result;
}
